*{
	margin:0;
	padding:0;
	font-family: "Noto Sans KR", sans-serif;
}

button{
	background:transparent;
	border:none;
	cursor:pointer;
}

.btn-full{
	height:40px;
	padding:0 10px;
	background-color:#004db1;
	color:#fff;
	border-radius:10px;
}

.btn-border{
	height:40px;
	padding:0 10px;
	color:#004db1;
	font-size:14px;
	border:1px solid #004db1;
	border-radius:10px;
	box-sizing:border-box;
}

.btn-under{
	color:#004db1;
	font-weight:500;
	text-decoration:underline;
}

.txtBx{
	width:300px;
	height:40px;
	padding:0 10px;
	border:1px solid #dedede;
	border-radius:10px;
	box-sizing:border-box;
}

.Title{
	width:calc(100% - 40px);
	height:50px;
	padding:0 20px;
	background-color:#222;
	color:#f1f1f1;
	font-size:20px;
	font-weight:500;
	line-height:50px;
}

.Contents{
	padding:20px;
}

.list{
	display:flex;
	flex-wrap: wrap;
	width: 80%;
	margin:0 auto;
	padding:20px;
	border:1px solid #ddd;
	border-radius:20px;
	box-sizing:border-box;

	.btn-border{
		margin-left:auto;
		margin-bottom:20px;
	}

	.item{
		width:100%;
		margin-bottom:10px;
		padding-bottom:10px;
		border-bottom:1px solid #222;

		.tit{
			float:left;
			font-size:20px;
			font-weight:700;
			cursor:pointer;
		}

		.like{
			margin-left:10px;
			font-size:14px;
		}

		.date{
			float:right;
			font-size:14px;
		}

		.cnt{
			float:left;
			width:100%;
			margin-top:5px;
			font-size:16px
		}

		&:after{
			display:block;
			clear:both;
			content:"";
		}
	}
	.modal{
		float:left;
		width:calc(100% - 40px);
		margin-top:20px;
		padding:20px;
		background:#ddd;
		border-radius:10px;
		
		&:after{
			display:block;
			clear:both;
			content:"";
		}

		button{
			float:right;
			margin-bottom:10px;
		}

		.cntWrap{
			float:left;
			width:100%;
		}

		.cntWrap > * {
			font-size:14px !important;
		}
	}

	.write{
		margin:20px auto 0;

		.txtBx{
			margin-right:10px;
		}
	}
}